import React from 'react';
import { Box, Grid, Paper, Typography } from '@mui/material';

const DashboardPanel = () => {
  const options = [
    { title: "ACEPTACIN DE DATOS BIOMTRICOS", description: "Aceptacin de Datos Biomtricos" },
    { title: "ACEPTACIN DE DESIGNACIN", description: "Aceptacin de Designacin" },
    { title: "ADMINISTRACIN DE PUNTOS DE VENTA Y DOMICILIOS", description: "Administracin de puntos de venta y domicilios" },
    { title: "ADMINISTRADOR DE RELACIONES DE CLAVE FISCAL", description: "Modificacin del Perfil. Alta de servicios. Incorporacin y Revocacin de Relaciones" },
    { title: "APORTES EN LNEA", description: "Consulta de la situacin personal en la seguridad social (previsional, obra social, riesgo del trabajo) respecto al destino de los aportes y las contribuciones sobre el salario" },
    { title: "ASPA - APLICACIN PARA SELECCIN DE PERSONAL AFIP", description: "Carga de Curriculum Vitae en AFIP" },
    { title: "AUTORIZACIN DE IMPRESIN DE COMPROBANTES", description: "Autorizacin de Impresin de Comprobantes" },
    { title: "BIENES PERSONALES WEB", description: "Presentacin de la declaracin jurada de bienes personales mediante servicio Web" },
    { title: "CARGA BILLETERA AFIP", description: "Carga de dinero en cuenta propia y de terceros para el cumplimiento de obligaciones" },
  ];

  return (
    <Box p={3}>
      <Grid container spacing={2}>
        {options.map((option, index) => (
          <Grid item xs={12} sm={6} md={4} key={index}>
            <Paper elevation={3} style={{ padding: '20px' }}>
              <Typography variant="h6">{option.title}</Typography>
              <Typography variant="body2">{option.description}</Typography>
            </Paper>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default DashboardPanel;
